@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.app{
    max-width: 1200px;
    margin: 0 auto
}
a{
    text-decoration: none;
    color:#000;
    color:initial;
}
a, p, div{
    font-family: 'Roboto', sans-serif;
}
.header__logo{
  cursor: pointer;
  -webkit-animation: left-slide 2s ease-in-out ;
          animation: left-slide 2s ease-in-out ;
}
.header{
  z-index: 2;
  background: white;
  width: 100vw;
  height: 80px;
  position: fixed;
  left: 0;
  top:0;
}
.header__content{
  display: flex;
  justify-content: space-between;
margin:0 auto;
  max-width: 1200px;
  width: 100%;
  align-items: center;
  height: 80px;
}
.menu{
  display: flex;
  list-style-type: none;
  -webkit-animation: right-slide 2s ease-in-out ;
          animation: right-slide 2s ease-in-out ;
}
.menu__element{
margin-left:20px ;
}

.menu__element__link{
  font-size:22px;
}

@-webkit-keyframes  right-slide {
  0% {
    transform: translateX(200px) rotate(0);
    opacity: 0;

  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translateX(-20px) rotate(-10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}

@keyframes  right-slide {
  0% {
    transform: translateX(200px) rotate(0);
    opacity: 0;

  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translateX(-20px) rotate(-10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}
@-webkit-keyframes left-slide {
  0% {
    transform: translateX(-200px) rotate(0);
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translateX(20px) rotate(10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}
@keyframes left-slide {
  0% {
    transform: translateX(-200px) rotate(0);
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    transform: translateX(20px) rotate(10deg);
  }
  100% {
    transform: translateX(0) rotate(0);
  }
}
.banner{
    position: absolute;
    left: 0;
    top: 80px;
width:100%;
height: calc(100vh - 80px);
overflow: hidden;
background-image:url(/Youngmedia/static/media/motto-bg.726f5726.jpg);
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
}
.banner__img{
width:100%;
}
.banner__quote{
    font-size: 70px;
    color: #fff;
    text-transform: uppercase;
    text-align: center;
  }
  .banner__author{
    font-style: italic;
    text-align: right;
    color:white;
  }

